import $ from 'jquery/dist/jquery.min.js';
import Swiper, { Navigation, Pagination } from 'swiper/core';
Swiper.use([Navigation, Pagination]);

export default () => {
	const $root = $('#deputiesSlider');

	if (!$root.length) return;

	const $swiperContainer = $root.find('.swiper-container');
	const $btnPrev = $root.find('.swiper-button-prev');
	const $btnNext = $root.find('.swiper-button-next');

	const config = {
		direction: 'vertical',
		speed: 500,
		navigation: {
			nextEl: $btnNext[0],
			prevEl: $btnPrev[0],
		},
	}

	const slider = new Swiper($swiperContainer[0], config);
};